import React from 'react';
import {BrowserRouter as Router, Route, Link } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import './App.css';

import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import SkillPage from './pages/SkillPage';
import ContactPage from './pages/ContactPage';

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      title: 'Fashion Brands',
      headerLinks: [
        { title: 'Home', path: '/' },
        { title: 'Skill', path: '/skill' },        
        { title: 'Contact', path: '/contact' }
      ],
      home: {
        title: "I'm RASHID A S ",
        subTitle: 'Front-end Developer',
        text: 'Goal-focused Front-end Developer, completed course in meanstack development. Passionate to learn contribute and grow along with the organisation.'
      },
      skill: {
        title: "Skill's",
      },
      contact: {
        title: "Let's Talk",
      }
    }
  }

  render() {
    return (
      <Router>
        <Container className="p-0" fluid={true}>
          
          <Navbar className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Brand className="head-title">Portfolio</Navbar.Brand>

            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle" />
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/">Home</Link>
                <Link className="nav-link" to="/skill">Skill</Link>
                <Link className="nav-link" to="/contact">Contact</Link>
              </Nav>
              </Navbar.Collapse>
          </Navbar>

                <Route path="/" exact render={() => <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />} /> 
                <Route path="/skill" render={() => <SkillPage title={this.state.skill.title} />} /> 
                <Route path="/contact" render={() => <ContactPage title={this.state.contact.title} />} /> 
          <Footer>

          </Footer>

        </Container>
      </Router>
    );
  }
}
export default App;