import React from 'react';
import Hero from '../components/Hero';
import Content from '../components/Content';

function SkillPage(props) {

    return(
        <div>
            <Hero className='hskill' title={props.title} />

            <Content>
            <ul className='skill'>
                <li>HTML</li>
                <li>CSS</li>
                <li>BOOTSTRAP</li>
                <li>ADOBE PHOTOSHOP</li>
                <li>JAVASCRIPT</li>
                <li>TYPESCRIPT</li>
                <li>ANGULAR 8</li>
                <li>REACT</li>
            </ul>
            </Content>
        </div>
    );
}

export default SkillPage;