import React from 'react';

import Card from '../components/Card';

import image1 from '../assets/images/shopping-cart-html-css.png';
import image2 from '../assets/images/cv_html-css.png';
// import image3 from '../assets/images/cv-react.png';
import image4 from '../assets/images/portfolio-react.png';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

class Carousel extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            items: [
                {
                    id: 0,
                    title: 'Shopping Cart',
                    subTitle: 'html,css,bootstrap',
                    imgSrc: image1,
                    link: 'https://',
                    selected: false
                },
                {
                    id: 1,
                    title: 'Portfolio',
                    subTitle: 'html,css,bootstrap',
                    imgSrc: image2,
                    link: 'https://',
                    selected: false
                },
                {
                    id: 2,
                    title: 'Portfolio',
                    subTitle: 'React',
                    imgSrc: image4,
                    link: 'https://',
                    selected: false
                },
                // {
                //     id: 3,
                //     title: 'Portfolio',
                //     subTitle: 'React',
                //     imgSrc: image4,
                //     link: 'https://',
                //     selected: false
                // },
            ]
        }
    }

    handleCardClick = (id, card) => {
        
        let items = [...this.state.items];

        items[id].selected =items[id].selected ? false : true;

        items.forEach(item => {
            if(item.id !== id) {
                item.selected = false;
            }
        });

        this.setState({
            items
        });
    }

    makeItems = (items) => {
        return items.map(item => {
            return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id} />
        })
    }

    render() {
        return(
            <Container fluid={true}>
                <Row className='justify-content-around'>
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }
}

export default Carousel;